<template>
  <div class="stuation padding_r_30">
    <div :class="['flex', 'padding_v_20', 'article_list', {border_b: k<content.length-1}]" v-for="c,k in content">
      <div class="">
        <img class="fit_img max_height" style="width:220px;height:150px" :src="c.news_img||image">
      </div>
      <div class="flex_1 padding_l_15">
        <div class="font_bold font_16">{{ c.news_title }}</div>
        <div class="lineheight_3 light"><span class="padding_r_10">{{ c.news_source }}</span>|<span class="padding_l_10">{{ c.created_at }}</span></div>
        <div class="line_ellipsis light">{{ c.news_brief }}</div>
      </div>
    </div>
  </div>
</template>

<script>
import api from '@/api'
export default {
  name: 'stuation',
  data () {
    return {
      content: [],
      image: require('@/assets/images/image.png')
    }
  },
  created () {
    api.post('detail/project_news', {sign: 'token=0&project_id=1'}).then(res => {
      console.log(res)
      this.content = res
    })
  }
}
</script>

<style type="text/css">
  .article_list.padding_v_20:first-child{
    padding-top: 0
  }
  .fit_img.max_height{
    height: 105px
  }
</style>



